export const getTextColor = (hexcolor) => {
    hexcolor = hexcolor.replace("#", "");
    var r = parseInt(hexcolor.substr(0,2),16);
    var g = parseInt(hexcolor.substr(2,2),16);
    var b = parseInt(hexcolor.substr(4,2),16);
    var yiq = ((r*299)+(g*587)+(b*114))/1000;
    return (yiq >= 128) ? '#000' : '#fff';
}

export const getDarkColor = (color, amount) => {
    return '#' + color.replace(/^#/, '').replace(/../g, color => ('0'+Math.min(255, Math.max(0, parseInt(color, 16) + amount)).toString(16)).substr(-2));
}

export const closeModal = (e) => {
    const targetElement = e.currentTarget.closest('#deleteModal');
    targetElement.classList.remove("show", "in");
}

export const openModal = (e) => {
    const targetId = e.currentTarget.getAttribute("data-target");
    document.getElementById(targetId).classList.add('show', 'in');
}