const notesDefaultState = []

const notesReducer = (state = notesDefaultState, action) => {
    switch (action.type) {
        case 'ADD_STICKY_NOTE':
            localStorage.setItem('notes', JSON.stringify([...state, action.data]));
            return [
                ...state,
                action.data
            ]
        case 'EDIT_STICKY_NOTE':
            return state.map((note, i) => {
                if (note.id === action.data.id) {
                    const local = state.filter(({id}) => id !== action.data.id);
                    localStorage.setItem('notes', JSON.stringify([...local, {
                        ...note,
                        ...action.data
                    }]));
                    return {
                        ...note,
                        ...action.data
                    };
                } else {
                    return note;
                };
            });
        case 'POPULATE_STICKY_NOTES':
            return action.data
        default:
            return state;
    }
}

export {notesReducer as default}