import React from 'react';
import {closeModal} from '../scripts/utils';



const DeleteStickyNote = () => (
    <div className="modal fade" id="deleteModal" role="dialog" aria-labelledby="myModalLabel">
        <div className="modal-dialog" role="document">
            <div className="modal-content">
                <div className="modal-header">
                    <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={closeModal}><span aria-hidden="true">&times;</span></button>
                    <h4 className="modal-title" id="myModalLabel">Are you sure you want to delete?</h4>
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-default" data-dismiss="modal" onClick={closeModal}>Close</button>
                    <button id="confirmDelete" type="button" className="btn btn-danger">Delete</button>
                </div>
            </div>
        </div>
    </div>
)

export default DeleteStickyNote;