
// ADD_STICKY
export const addStickyNotes = ({bgcolor,
    bgdesign,
    pin,
    textcolor,
    xaxis,
    yaxis,
    data,
    width,
    height,
    zindex,
    id,
    status} = {}) => (
    {
        type: 'ADD_STICKY_NOTE',
        bgcolor,
        bgdesign,
        pin,
        textcolor,
        xaxis,
        yaxis,
        data,
        width,
        height,
        zindex,
        id,
        status
    }
);

// POPULATE_STICKY
export const populateStickyNotes = (data) => (
    {
     type: 'POPULATE_STICKY_NOTES',
     data
    }
);


// EDIT_STICKY
export const editStickyNotes = (data) => ({
    type: 'EDIT_STICKY_NOTE',
    data
})