import React from 'react';
import { connect } from 'react-redux';
import { editStickyNotes } from '../actions/sticky';

const MyNotes = (props) => (
    <li className="active treeview">
        <a href="#">
            <i className="fa fa-sticky-note"></i> <span>My Notes</span> <i className="fa fa-angle-left pull-right"></i>
        </a>
        <ul className="treeview-menu">
            {props.notes.map(({status, id, xaxis, yaxis, width, height, bgcolor}, i) => {
                const xpos = xaxis;
                const ypos = yaxis;
                const wid = width;
                const hei = height;
                const bg = bgcolor;
                if(status === 1) {
                    return (
                        <li className="active" key={i}>
                            <a href="#"><i className="fa fa-circle-o text-green"></i> Sticky {i + 1}</a>
                        </li>
                    )
                } else if(status === 2) {
                    return (
                        <li key={i} onClick={(e) => {
                            console.log(xpos);
                            e.currentTarget.lastChild.style.cssText = `opacity: .1; transform: translate(${xpos + 244}px, ${ypos -178}px); width: ${wid}px; height: ${hei}px; background-color: ${bgcolor}`;
                            setTimeout(() => {
                                props.dispatch(editStickyNotes({status:1, id}))
                            }, 100)
                        }}>
                            <a href="#"><i className="fa fa-circle-o text-yellow"></i> Sticky {i + 1} <i title="maximize" className="fa fa-external-link-square pull-right"></i></a>
                            <div className="maximize"></div>
                        </li>
                    )
                }
                
            })}
        </ul>
    </li>
)

const mapStateToProps = (state) => {
    return {
      notes: state.notes
    };
  };
  
export default connect(mapStateToProps)(MyNotes);