import React from 'react';
import { connect } from 'react-redux';
import { addStickyNotes } from '../actions/sticky';
import { v4 as uuidv4 } from 'uuid';

const sticky = {
    "bgcolor": "#ffff88",
    "bgdesign": 1,
    "pin": 1,
    "xaxis": 0,
    "yaxis": 0,
    "data": "<p>data</p><h2>title</h2>",
    "width": 330,
    "height": 300,
    "zindex": 1,
    "id": '',
    "status": 1 // 1 - active, 2 - minimized, 3 - deleted, 4 - deleted permanently
}

const AddSticky = (props) => {
    const addNotes = () => {
        props.dispatch(addStickyNotes({
            data: {...sticky, ...{id:uuidv4()}}
        }));
    }

    return (
        <button onClick={addNotes} type="button" className="btn btn-block btn-sm">
            <i className="fa fa-fw fa-sticky-note"></i>Add Sticky Note
        </button>
    )
}
export default connect()(AddSticky);