import React, {useEffect, useState} from 'react';
import { connect } from 'react-redux';
import Draggable from 'react-draggable';
import { populateStickyNotes, editStickyNotes } from '../actions/sticky';
import { getTextColor, getDarkColor, openModal } from '../scripts/utils';


const StickyNote = (props) => {
    const {notes, dispatch} = props;
    const [noteChange, setNoteChange] = useState({})
    const [currentId, setCurrentId] = useState('');
    const [confirmation, setConfirmation] = useState(false);
    // const [resize, setResize] = useState(false);
    
    // Get data from database
    useEffect(() => {
        const data = JSON.parse(localStorage.getItem('notes'));
        if(data) {
            dispatch(populateStickyNotes(data))
        }
    }, []);

    // Call to store for Edit
    useEffect(() => {
        if(Object.keys(noteChange).length !== 0){
            dispatch(editStickyNotes(noteChange));
            setNoteChange({});
        }
    }, [noteChange]);

    // Delete
    useEffect(() => {
        if(confirmation){
            setNoteChange({
                ...noteChange,
                ...{status: 3, id: currentId}
            });
            setCurrentId('');
            setConfirmation(false);
        }
    }, [confirmation]);

    // Delete confirmation
    window.onload=function(){
        document.getElementById('confirmDelete').addEventListener('click', () => {
            setConfirmation(true);
            document.getElementById("deleteModal").classList.remove('show', 'in');
        });
    }

    // Top Z index
    const visibleTop = (e) => {
        document.querySelector('.box').style.zIndex = 0;
        e.currentTarget.style.zIndex = 1;
    }

    // useEffect(() => {
    //     notes.map(({id, xaxis, status, width}) => {
    //         let getXpos = stickyAreaWidth - width- 40;
    //         if(status === 1) {
    //             if(getXpos < xaxis){
    //                 console.log(getXpos);
    //                 setNoteChange({
    //                     ...noteChange,
    //                     ...{xaxis:getXpos, id}
    //                 });
    //             }
    //         }
    //     });
    // }, [resize])

    // const stickyBound = () =>{
    //     const stickyAreaWidth = document.querySelector('.sticky-wrapper').offsetWidth;
    //     console.log(stickyAreaWidth)
        
    // }

    // let resizeTimer;

    // const resizeEvent = () => {
    //     clearTimeout(resizeTimer);
    //     resizeTimer = setTimeout(stickyBound, 250);
    // }

    // window.addEventListener("resize", resizeEvent);

    // const nodeRef = React.useRef(null);

    return (
        notes.map(({id, xaxis, yaxis, bgcolor, status}, i) => {
            if(status === 1) {
                return (
                    <Draggable
                        key={i}
                        handle=".box-header"
                      //  defaultPosition={{x: xaxis, y: yaxis}}
                        position={{x: xaxis, y: yaxis}}
                        // grid={[25, 25]}
                        scale={1}
                        bounds="parent"
                        // nodeRef={nodeRef}
                        // onStart={this.handleStart}
                        // onDrag={this.handleDrag}
                        onStop={(e, pos)=> {
                            if(xaxis !== pos.x || yaxis !== pos.y){
                                setNoteChange({
                                    ...noteChange,
                                    ...{xaxis:pos.x, yaxis:pos.y, id}
                                });
                            }
                        }}
                    >
                        <div className="box"  style={{backgroundColor:bgcolor, color:getTextColor(bgcolor), borderTopColor:getDarkColor(bgcolor, -50)}} onClick={visibleTop}>
                            <div className="box-header">
                                <div className="box-title">
                                    <label className="color-picker">
                                        <input type="color" value={bgcolor} onChange={(e) => {
                                            setNoteChange({
                                                ...noteChange,
                                                ...{bgcolor: e.target.value, id}
                                            });
                                        }} />
                                    </label>
                                   <span> Sticky - {i+1} </span>
                                </div>
                                <div className="box-tools pull-right" style={{color:getTextColor(bgcolor)}}>
                                    <button type="button" title="Minimize" className="btn btn-box-tool" data-widget="collapse" onClick={(e) => {
                                        e.currentTarget.closest('.box').classList.add('minimize');
                                        setTimeout(() => {
                                            setNoteChange({
                                                ...noteChange,
                                                ...{status: 2, id}
                                            });
                                        }, 300);
                                    }}>
                                        <i className="fa fa-minus"></i>
                                    </button>
                                    <button id="del" type="button" className="btn btn-box-tool" data-toggle="tooltip" title="Lock" data-widget="chat-pane-toggle">
                                        <i className="fa fa-lock"></i>
                                    </button>
                                    <button type="button" className="btn btn-box-tool" data-target="deleteModal" data-widget="remove" onClick={(e) => {
                                        setCurrentId(id);
                                        openModal(e);
                                    }}>
                                        <i className="fa fa-trash-o"></i>
                                    </button>
                                </div>
                            </div>
                            <div className="box-body">
                                <p>Note ID: {id}</p>
                                <p>git fetch --all<br/>git status<br/>git add .<br/>git commit -m "commit message"<br/>git push -u origin dev/add-sticky</p>
                            </div>
                        </div>
                    </Draggable>
                )
            }
        })
    )
};

const mapStateToProps = (state) => {
    return {
      notes: state.notes
    };
  };
  
export default connect(mapStateToProps)(StickyNote);